.section {
    background-color: white;
    box-shadow: 1px -1px 5px 0px #DDDDDD;
}

.section__title {
    padding: 20px 0;
    margin: 0;
    font-size: 20px;
}

.section__header {
    display: flex;
    border-bottom: 1px solid #ddd;
    margin-top: 10px;
    padding: 0 20px;
}

@media (max-width:768px) {
    .section__header {
        flex-direction: column;
    }
}